  <div class="home">
    <div :class="[{'headWrapper':topsc},{'fontsS':cn},{'fonte':en}]">
      <div class="headNav">
        <div class="leftContent">
          <img src="../assets/imgs/logo.png" alt />
          <div class="changeLan">
            <div>
              <img v-show="cn" src="../assets/imgs/中文.png" alt />
              <img v-show="en" src="../assets/imgs/英文.png" alt />
            </div>
            <span class="point">·</span>
            <div style="position:relative;width:1.5rem">
              <div v-show="cn" @click="changelang()">简体中文</div>
              <div v-show="en" @click="changelang()">English</div>
              <div v-show="listshow" class="langChange">
                <div @click="changetable('cn')">简体中文</div>
                <div @click="changetable('en')">English</div>
              </div>
            </div>
          </div>
        </div>
        <div @click="showNav()" class="rightContent">
          <div>
            <span v-show="!navcontrol" class="firstLine"></span>

            <span v-show="!navcontrol" class="secondLine"></span>

            <span v-show="!navcontrol" class="thirdLine"></span>
            <img
              style="width:0.75rem;height:0.75rem"
              v-show="navcontrol"
              src="../assets/imgs/nav.png"
              alt
            />
            <div v-show="!navcontrol">{{$t('nav.main')}}</div>
          </div>
        </div>

        <div @click="showNav()" v-show="navcontrol" class="topnav"></div>

        <transition name="top">
          <ul v-show="navcontrol" class="navlist">
            <li :class="{'active':nowpath=='/'}" @click="changeView('/')">{{$t('nav.HOME')}}</li>
            <li
              :class="{'active':nowpath=='/Dynamic'}"
              @click="changeView('/Dynamic')"
            >{{$t('nav.NEWS')}}</li>
            <li
              :class="{'active':nowpath=='/Wallet'}"
              @click="changeView('/Wallet')"
            >{{$t('nav.Wallet')}}</li>
            <li
              :class="{'active':nowpath=='/Tec'}"
              @click="changeView('/Tec')"
            >{{$t('nav.Technology')}}</li>
            <li
              :class="{'active':nowpath=='bowser'}"
              @click="changeOpen('bowser')"
            >{{$t('nav.Explorer')}}</li>
            <li
              :class="{'active':nowpath=='/Apply'}"
              @click="changeView('/Apply')"
            >{{$t('nav.Application')}}</li>
            <li
              :class="{'active':nowpath=='/Aboutsus'}"
              @click="changeView('/Aboutsus')"
            >{{$t('nav.About')}}</li>
            <li :class="{'active':nowpath=='book'}" @click="openBook()">{{$t('nav.book')}}</li>
          </ul>
        </transition>
      </div>
    </div>
    <div class="swiperwrapper">
      <van-swipe :autoplay="3000" indicator-color="white">
        <van-swipe-item>
          <div class="firstWrap">
            <div>
              <p></p>
              <p></p>
            </div>
          </div>
        </van-swipe-item>
        <van-swipe-item>
          <div class="secondWrap">
            <div>
              <p></p>
              <p></p>
            </div>
          </div>
        </van-swipe-item>
        <van-swipe-item>
          <div class="thirdWrap">
            <div>
              <p></p>
              <p></p>
            </div>
          </div>
        </van-swipe-item>
      </van-swipe>
    </div>
    <router-view></router-view>

    <Bottom></Bottom>
  </div>
</template>

<script>
import Bottom from "../components/Bottom";
import { book } from "../../api";
export default {
  components: {
    Bottom
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
    this.nowpath = this.$route.path;

    this.nowpath = "/";
    localStorage.setItem("locale", this.$i18n.locale);
    this.en = localStorage.getItem("locale") == "en" ? true : false;
    this.cn = localStorage.getItem("locale") == "zh" ? true : false;
  },
  beforeUpdate() {
    this.en = localStorage.getItem("locale") == "en" ? true : false;
    this.cn = localStorage.getItem("locale") == "zh" ? true : false;
  },
  methods: {
    showNav() {
      this.navcontrol = !this.navcontrol;
    },
    handleScroll() {
      let scrollY = window.scrollY;
      // console.log(scrollY);
      if (scrollY > 1) {
        this.topsc = true;
      } else {
        this.topsc = false;
      }
    },
    changeView(p) {
      this.$router.push(p);
      this.navcontrol = false;
      this.nowpath = p;
    },
    changeOpen(u) {
      if (u == "bowser") {
        window.location.href = "http://ex.sguchain.io";
      }
    },
    changelang() {
      this.listshow = !this.listshow;
    },
    changetable(lang) {
      if (lang == "cn") {
        this.listshow = !this.listshow;
        this.cn = true;
        this.en = false;
        this.$i18n.locale = "zh";
        localStorage.setItem("locale", "zh");
      } else {
        this.listshow = !this.listshow;
        this.cn = false;
        this.en = true;
        this.$i18n.locale = "en";
        localStorage.setItem("locale", "en");
      }
    },
    openBook() {
      if (this.cn == true) {
        book(1)
          .then(res => {
            let opurl = res.data.data[0].downUrl;
            window.open(opurl);
          })
          .catch(err => {
            window.console.log(err);
          });
      } else {
        book(2)
          .then(res => {
            let opurl = res.data.data[0].downUrl;
            window.location.href = opurl;
          })
          .catch(err => {
            window.console.log(err);
          });
      }
    }
  },
  data() {
    return {
      navcontrol: false,
      topsc: false,
      nowpath: "/",
      cn: true,
      en: false,
      listshow: false
    };
  }
};
</script>

