import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Invite from '../views/H5'
Vue.use(VueRouter)

const routes = [{
    path: '/',
    component: Home,

    children: [{
        path: '/Apply',
        component: () =>
            import ('../views/Apply')
    }, {
        path: '/Aboutsus',
        component: () =>
            import ('../views/Aboutsus'),
        children: [{
            path: '/Aboutsus/prombles',
            component: () =>
                import ('../views/AboutusPages/Prombles.vue')
        }, {
            path: '/Aboutsus/panter',
            component: () =>
                import ('../views/AboutusPages/Panter.vue')
        }],
        redirect: '/Aboutsus/panter'
    }, {
        path: '/Dynamic',
        component: () =>
            import ('../views/Dynamic'),
        children: [{
            path: '/Dynamic/announce',
            component: () =>
                import ('../views/DynamicPages/Announce.vue')
        }, {
            path: '/Dynamic/commuinity',
            component: () =>
                import ('../views/DynamicPages/Community.vue')
        }, {
            path: '/Dynamic/info',
            component: () =>
                import ('../views/DynamicPages/moreinfo/Info.vue')
        }],
        redirect: '/Dynamic/announce'
    }, {
        path: '/Tec',
        component: () =>
            import ('../views/Tec')
    }, {
        path: '/Wallet',
        component: () =>
            import ('../views/Wallet'),
        children: [{
            path: '/Wallet/ios',
            component: () =>
                import ('../views/wallet/Ios.vue')
        }, {
            path: '/Wallet/android',
            component: () =>
                import ('../views/wallet/Android.vue')
        }, ],
        redirect: '/Wallet/android'
    }, {
        path: '/Main',
        component: () =>
            import ('../views/MainPage')
    }],
    redirect: '/Main'

}, {
    path: '/invite',
    component: Invite
}]

const router = new VueRouter({
    mode: 'hash',
    routes
})

export default router